// common mixins - start

@mixin qodefBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin qodefImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include qodefAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            transition: opacity .2s ease-in-out;
        }

    } @else {
        @include qodefAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        transition: opacity .2s ease-in-out;
    }
}

@mixin qodefStandardPaginationStyle($list_type: null) {
    @include qodefRelativeHolderLayout(top);
    margin: 40px 0 0;
    clear: both;

    ul {
        @include qodefRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 12px;

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0;
                font-family: $heading-text-font;
                font-weight: 700;
                font-size: 16px;
                text-transform: uppercase;
                color: $default-heading-color;

                &:hover {
                    color: $first-main-color;
                }
            }

            &.qodef-pag-active {
                
                a {
                    color: $first-main-color;
                }
            }

            &.qodef-pag-prev,
            &.qodef-pag-next,
            &.qodef-pag-first,
            &.qodef-pag-last {
                margin: 0 2px;

                a {
                    font-size: 20px;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            @if ($list_type == 'shortcode') {
                
                &.qodef-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.qodef-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: $first-main-color;
                    font-family: $heading-text-font;
                    font-weight: 700;
                    font-size: 16px;
                    text-transform: uppercase;
                }

                a {
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
                            font-size: 20px;
                            line-height: 25px;
                            font-weight: normal;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    &.prev {
                        margin-right: -10px;

                        &:before {
                            content: "\34";
                        }
                    }

                    &.next {
                        margin-left: -10px;

                        &:before {
                            content: "\35";
                        }
                    }
                }
            }
        }
    }
}

@mixin qodefButtonDefaultStyle() {
    font-family: $heading-text-font;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-size: 14px;
    line-height: 2.1em;
    font-weight: 600;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;
    border-radius: 0;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
}

@mixin qodefButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 10px 43px;
        font-size: 12px;

    } @else if ($size == 'medium') {
        padding: 10px 45px;

    } @else if ($size == 'large') {
        font-size: 16px;
        padding: 10px 47px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        font-size: 16px;
        text-align: center;
        padding: 13px 26px;
    }
}

@mixin qodefButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin qodefButtonSolidColor($important: null) {
    color: $default-heading-color $important;
    background-color: $first-main-color $important;
    border: 1px solid transparent $important;
}

@mixin qodefButtonSolidHoverColor($important: null) {
    color: $default-heading-color $important;
    background-color: $lighter-main-hover-color $important;
}

@mixin qodefButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin qodefButtonOutlineHoverColor($important: null) {
    color: $default-heading-color $important;
    background-color: $first-main-hover-color $important;
    border-color: $lighter-main-hover-color $important;
}

@mixin qodefPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

