// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 #{$input-margin}px;
    padding: #{$input-vertical-padding}px #{$input-horizontal-padding}px;
    font-family: inherit;
    font-size: 15px;
    font-weight: inherit;
    line-height: calc(#{$input-height}px - (#{$input-vertical-padding * 2}px - 2px));
    color: $default-heading-color;
    background-color: transparent;
    border: 1px solid #000;
    border-radius: 0;
    outline: 0;
    cursor: text;
    -webkit-appearance: none;
    box-sizing: border-box;
    transition: border-color 0.2s ease-in-out;

    &:focus {
        color: $default-heading-color;
    }

    @include qodefPlaceholder {
        color: inherit;
    }
}

// mixins styles - end

