// animation mixin - start

@mixin keyframes($name) {
    @-webkit-keyframes #{$name} {
        @content;
    }

    @keyframes #{$name} {
        @content;
    }
}

@mixin qodefFlickerFix() {
    will-change: transform;
    -webkit-filter: blur();
    filter: blur();
}

@mixin qodefImageZoomHoverStyle($scale: 1.03, $hover-duration: .75s, $hover-out-duration: .45s) {

    overflow: hidden;
    @include qodefFlickerFix();

    img {
        backface-visibility: hidden;
        transform-origin: top;
        transition: transform $hover-out-duration cubic-bezier(.33,.02,.0,.93);

        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
            backface-visibility: visible;
        }
    }

    &:hover,
    &.qodef-hovered {

        img {
            transform: scale($scale);
            transition: transform $hover-duration cubic-bezier(.33,.02,0,.93);
        }
    }
}

@mixin qodefUnderline($bottom: 7px, $top: 22px, $width: 72px, $height: 3px, $background: #fed620) {
    position: relative;
    display: inline-block;

    &:after {
        content: '';
        left: 0;
        margin-bottom: $bottom;
        margin-top: $top;
        width: $width;
        height: $height;
        background-color: $background;
        display: block;
    }
}

// animation mixin - end

