// heading mixins - start

@mixin qodefDefaultHeadingStyle() {
    @include qodefHeadingStyle();
    margin: 25px 0;

    -ms-word-wrap: break-word;
    word-wrap: break-word;
    
    a {
        @include qodefTypographyLayout();
        
        &:hover {
            //color: $first-main-color;
            color: inherit;
        }
    }
}

@mixin qodefHeadingStyle($with-heading: null, $with-color: true) {

    font-family: $heading-text-font;
    font-weight: 700;
    text-transform: uppercase;
    
    @if ($with-color) {
        color: $default-heading-color;
    }
    
    @if ($with-heading == 'h1') {
        @include qodefH1();
    } @else if ($with-heading == 'h2') {
        @include qodefH2();
    } @else if ($with-heading == 'h3') {
        @include qodefH3();
    } @else if ($with-heading == 'h4') {
        @include qodefH4();
    } @else if ($with-heading == 'h5') {
        @include qodefH5();
    } @else if ($with-heading == 'h6') {
        @include qodefH6();
    }
}
@mixin qodefBody($important:null) {
    font-family: $default-text-font $important;
    font-size: 16px $important;
    font-weight: 400 $important;
    line-height: 1.5625em $important;
    color: $default-text-color $important;
    background-color: $default-background-color $important;
    -webkit-font-smoothing: antialiased $important;
    
    p {
        line-height: 1.5625em;
    }
}

@mixin qodefH1($important:null) {
    font-size: 60px $important;
    line-height: 1.05em $important;
    letter-spacing: -0.01em $important;
}

@mixin qodefH2($important:null) {
    font-size: 50px $important;
    line-height: 1.06em $important;
    letter-spacing: -0.01em $important;
}

@mixin qodefH3($important:null) {
    font-size: 30px $important;
    line-height: 1.33em $important;
    letter-spacing: -0.01em $important;
}

@mixin qodefH4($important:null) {
    font-size: 26px $important;
    line-height: 1.154em $important;
    letter-spacing: -0.01em $important;
}

@mixin qodefH5($important:null) {
    font-size: 20px $important;
    line-height: 1.25em $important;
    letter-spacing: -0.025em $important;
}

@mixin qodefH6($important:null) {
    font-size: 16px $important;
    line-height: 1.60em $important;
    letter-spacing: -0.025em $important;
}

// heading mixins - end

@mixin qodefStrokeFont() {
	color: transparent;
	-webkit-text-stroke: 1px $default-heading-color;
	
	@media all and (-ms-high-contrast: none) {
		//IE Text stroke replacement
		color: #fff;
		text-shadow: -1px -1px 0px $default-heading-color, 1px 1px 0px $default-heading-color, -1px 1px 0px $default-heading-color, 1px -1px 0px $default-heading-color;
	}
	
}

